export default {
	data() {
		return {
			base:{
				title:this.$config.title,
				logo:this.$config.logo
			},
			verifyTime: 0,
			loadModal:false,
			modalName:'',
			indexTab:1,
			isRotate:false,
			param: {
				account: 'admin',
				password: '123456',
				mobile: '17080057443',
				captcha: '1111',
			},
			wechatPath: {
				encryptedData: '',
				iv: '',
				code: false
			}
		}
	},
	methods: {
		wechatBtn(){
			this.modalName = 'wechat';
		},
		wechatMpLogin(e) {
			this.loadModal = true;
			let _this = this;
			let modalName = _this.modalName;
			
			// 隐藏登陆
			_this.modalName = '';
			// 显示等待
			// _this.loadModal = true;
			// 获取关键内容
			_this.wechatPath.iv = e.detail.iv;
			_this.wechatPath.encryptedData = e.detail.encryptedData;
			// 请求小程序登陆接口
			_this.$api.wechatLogin(_this.wechatPath, res => {
				if (res.code) {
					// 保存登陆并返回
					_this.$common.saveUserInfo(res.data);
					_this.loginSuccess();

				} else {
					_this.$common.errorToShow(res.msg);
					_this.hideModal();
					_this.modalName = modalName;						
					_this.getCode();
				}
			});
		},
		
		// 获取登陆代码
		getCode() {
			uni.login({
				success: res => {
					console.log('wechatPath', res);
					if (res.code) {
						this.wechatPath.code = res.code;
					} else {
						this.$common.errorToShow('用户未授权code');
					}
				},
				fail: res => {
					this.$common.errorToShow('用户授权失败');
				}
			});
		},
		hideModal() {
			this.loadModal = null;
			this.modalName = null;
		},
		navigateTo(url) {
			uni.navigateTo({
				url: url
			})
		},
		loginSuccess(){
			setTimeout(function() {
				uni.reLaunch({
					url:'/pages/app/indexs'
				})
			}, 1000);
			
		},
		Toggle(index){
			console.log('test')
			this.indexTab=index;
		},
		// 登录
		login() {
			// 判断是否加载中，避免重复点击请求
			if (this.param.account == '' || this.param.password.length < 6) {
				this.$common.errorToShow('账号或密码错误');
			} else {
				// 显示等待
				this.loadModal = true;
				setTimeout(() => {
					this.$api.login(this.param, res => {
						this.$common.errorToShow(res.msg);
						if (res.code) {
							this.$common.saveUserInfo(res.data);
							this.loginSuccess();
						}
					}, () => {
						this.loadModal = false;
					});
				}, 1000);
			}
		},
	},
	onLoad() {			

		this.getCode();

		console.log('onLoad')
	}
}
